import React from "react";
import '../index.css';

const Home = ({ }) => {
    return (
        <div className="d-flex section" style={{ minHeight: "100vh"}} id="Home">
            <div className="my-auto row">
                <div className="col-7">
                    <h1 style={{ fontWeight:600 }} className="row display-1 fadeInLeft">
                        NIHAL SHAH
                    </h1>
                    <h5 style={{ fontWeight:600, animationDuration:"500ms" }} className="row fadeInLeft">
                        Data Science and Computer Science
                    </h5>
                    <p className="row fadeInLeft fs-6" style={{ animationDuration:"400ms"}}>
                        A software engineer working to expand the work done in
                        cutting-edge computer science fields utilizing complex procedures
                        to develop usable solutions to problems, with the goal of making
                        the world a better place to be in.
                    </p>
                </div>
                <img src="img/spraxa.jpg" className="img-fluid col-3 rounded-circle" alt=""/>
            </div>
        </div>
    );
};

export default Home;